import React, { Component } from 'react';
import {Layout,Row,Col,Icon} from 'antd'
import HamBurger from '../images/hamburger.png'
import {COLORS} from "../constants"
import SettingsText from './preview-components/text';
import SettingsNumber from './preview-components/number';
import SettingsScale from './preview-components/scale';
import SettingsLocation from './preview-components/location';
import SettingsDate from './preview-components/date';
import SettingsFile from './preview-components/file';
import SettingsDropDown from './preview-components/dropdown';
import SettingsEmail from './preview-components/email';
import SettingsMatrix from './preview-components/matrix';
import SettingsPrice from './preview-components/price';
import SettingsSign from './preview-components/sign';
import SettingsImage from './preview-components/image';
import SettingsScan from './preview-components/scan';
class Preview extends Component {
  render() {
    let {currentQuestion,currentIndex}= this.props;
    let currentComponent = null;
    let backgroundColor = COLORS.APPCOLOR;
    if(currentQuestion)
    switch (currentQuestion.type) {
      case "text":
        currentComponent = <SettingsText listItem={currentQuestion} index={currentIndex}/>
        break
      case "number":
        currentComponent = <SettingsNumber listItem={currentQuestion} index={currentIndex}/>
        break
      case "scale":
        currentComponent = <SettingsScale listItem={currentQuestion} index={currentIndex}/>
        break
      case "location" :
        currentComponent = <SettingsLocation listItem={currentQuestion} index={currentIndex}/>
        break
      case "date" :
        currentComponent = <SettingsDate listItem={currentQuestion} index={currentIndex}/>
        break
      case "file" :
        currentComponent = <SettingsFile listItem={currentQuestion} index={currentIndex}/>
        break
      case "dropdown" :
        currentComponent = <SettingsDropDown listItem={currentQuestion} index={currentIndex}/>
        break
      case "email" :
        currentComponent = <SettingsEmail listItem={currentQuestion} index={currentIndex}/>
        break
      case "matrix" :
        currentComponent = <SettingsMatrix listItem={currentQuestion} index={currentIndex}/>
        break
      case "price" :
        currentComponent = <SettingsPrice listItem={currentQuestion} index={currentIndex}/>
        break
      case "sign" :
        currentComponent = <SettingsSign listItem={currentQuestion} index={currentIndex}/>
        break
      case "image" :
        currentComponent = <SettingsImage listItem={currentQuestion} index={currentIndex}/>
        break
      case "scan" :
        currentComponent = <SettingsScan listItem={currentQuestion} index={currentIndex}/>
        break

      default:
        currentComponent = <SettingsText listItem={currentQuestion} index={currentIndex}/>

    }

    return (
      <Row className="create-question-container">
          <Row className="preview-header">
            <Col span={4}>
              <Icon type="bars" style={{fontSize:'30px',color:'#13C64F',margin:10}} />
            </Col>
            <Col style={{paddingTop:12}}>
              <h3>{'Survey'}</h3>
            </Col>
          </Row>
          {
            (currentQuestion)?
              currentComponent
            :
            <Row className="preview-body" style={{background:backgroundColor}}>
                <div style={{marginTop:'55%',fontSize:25,color:'white',marginBottom:40,textAlign:'center'}}>
                {'Create a question'}
                </div>
            </Row>
          }
      </Row>
    );
  }
}

export default Preview;
