import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import {connect} from 'react-redux'
import {updateQuestion,copyQuestion,deleteQuestion,deleteSubQuestion} from '../../actions/createAction';
import QuestionText from '../sub-components/text';
import QuestionNumber from '../sub-components/number';
import QuestionScale from '../sub-components/scale';
import QuestionLocation from '../sub-components/location';
import QuestionDate from '../sub-components/date';
import QuestionFile from '../sub-components/file';
import QuestionDropDown from '../sub-components/dropdown';
import QuestionEmail from '../sub-components/email';
import QuestionMatrix from '../sub-components/matrix';
import QuestionPrice from '../sub-components/price';
import QuestionSign from '../sub-components/sign';
import QuestionImage from '../sub-components/image';
import QuestionScan from '../sub-components/scan';
class SubGroupItem extends Component {
  delete = () => {
    let {questionIndex,index,parentIndex} = this.props;
    console.log(questionIndex,index,parentIndex)
    this.props.deleteSubQuestion(questionIndex,parentIndex,index)
  }
  render() {
    let {listItem,index} =this.props;
    switch (listItem.type) {
      case "text":
        return <QuestionText listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "number":
        return <QuestionNumber listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "scale":
        return <QuestionScale listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "location" :
        return <QuestionLocation listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "date" :
        return <QuestionDate listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "file" :
        return <QuestionFile listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "dropdown" :
        return <QuestionDropDown listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "email" :
        return <QuestionEmail listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "matrix" :
        return <QuestionMatrix listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "price" :
        return <QuestionPrice listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "sign" :
        return <QuestionSign listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "image" :
        return <QuestionImage listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>
      case "scan" :
        return <QuestionScan listItem={listItem} index={index} parentIndex={this.props.parentIndex} questionIndex={this.props.questionIndex} delete={this.delete}/>

      default:
        return <QuestionText listItem={listItem} index={index} parentIndex={this.props.parentIndex}/>
    }
  }
}
const mapDispatchToProps = {
  updateQuestion,
  copyQuestion,
  deleteQuestion,
  deleteSubQuestion
}
export default connect(null,mapDispatchToProps)(SubGroupItem);
