import store from '../store';
import {updateQuestions,updateQuestion} from '../actions/createAction'
export const addQuestion = (type) =>{
  let storeQuestions =store.getState().create.questions;
  let questions = [...storeQuestions];

  let questionTemplate = {
    question:"Type Your Question ...",
    option:"Description",
    type:type,
    settings:{},
    preview:{},
    subGroup:[],
  }
  questions.push(questionTemplate);
  console.log(questions);
  store.dispatch(updateQuestions(questions));
}
export const addSubQuestion = (type,index) =>{
  let storeQuestions =store.getState().create.questions;
  let questions = [...storeQuestions];
  if(index!=undefined){

    let question = {...questions[index]};

    if(questions[index]!=undefined){
        let questionTemplate = {
          question:"Type Your Question ...",
          option:"Description",
          type:type,
          settings:{},
          preview:{},
          settingsOpen:false,
        }

        let newSubGroup =question.subGroup;
        newSubGroup.push([questionTemplate]);
        question.subGroup = newSubGroup;


        store.dispatch(updateQuestion(question,index));
    }
  }
}
export const addSubGroupQuestion = (type,index,subGroupIndex) =>{
  let storeQuestions =store.getState().create.questions;
  let questions = [...storeQuestions];
  if(index!=undefined){

    let question = {...questions[index]};

    if(questions[index]!=undefined && question.subGroup[subGroupIndex]!=undefined){
        let questionTemplate = {
          question:"Type Your Question ...",
          option:"Description",
          type:type,
          settings:{},
          preview:{},
          settingsOpen:false,
        }

        let newSubGroup =[...question.subGroup];
        newSubGroup[subGroupIndex].push(questionTemplate);
        question.subGroup = newSubGroup;
        console.log(question,"/login => addSubGroupQuestion()")

        store.dispatch(updateQuestion(question,index));
    }
  }
}
