import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Dropdown} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import ScaleBadge from '../../images/scale-badge.svg'
import {connect} from 'react-redux'
import {COLORS} from  "../../constants"
import {updateQuestion,copyQuestion,deleteQuestion} from '../../actions/createAction';
import {Badge,FpBadgeMenu} from "../../fp-components/fp-minor"
import SubGroup from '../sub-group'
class QuestionScale extends Component {
  updateQuestion = (event) => {
    let {index}=this.props;
    //console.log(this.props.index);
    let questionObj = Object.assign({},this.props.listItem,{question:event.currentTarget.value});
    this.props.updateQuestion(questionObj,index);
  }
  updateAnswer = (event) => {
    let {index}=this.props;
    //console.log(this.props.index);
    let questionObj = Object.assign({},this.props.listItem,{option:event.currentTarget.value});
    this.props.updateQuestion(questionObj,index);
  }
  render() {
    return (
      <Row className="create-question-item-container ">
          <Row>
          <Col span={4} className="badge-container">
              <Badge text={<span><img src={ScaleBadge} style={{padding:'5px 7px 6px 1px'}} />{`${parseInt(this.props.index)+1}`}</span>} color={COLORS.SCALE_TYPE} />
          </Col>
            <Col span={20}>
              <Input value={this.props.listItem.question} className="create-question-input " onChange={(e)=>this.updateQuestion(e)}/>
            </Col>
          </Row>
          <Row className="second-row-container">
            <Col span={6}>
            </Col>
            <Col span={18}>
              {
                (this.props.listItem.settings.description)?
                <Input value={this.props.listItem.option} className="create-question-input-text-answer" onChange={(e)=>this.updateAnswer(e)}/>
                :
                null
              }
            </Col>
          </Row>
          {
            (this.props.listItem.settings.subGroup)?
            (
              <Row>
                <Row>
                  <SubGroup subgroup={[...this.props.listItem.subGroup]} index={this.props.index}/>
                </Row>
                <Row className="new-subgroup">
                    <Col span={3}>
                    </Col>
                    <Col span={8}>
                      <Dropdown overlay={FpBadgeMenu(this.props.index)} >
                        <div className="new-subgroup-box">
                          New Subgroup
                        </div>
                      </Dropdown>
                    </Col>
                    <Col span={13}></Col>
                </Row>
              </Row>
            )
            :
            null
          }
          <Row>

            <Col span={1} offset={22}>
              <Icon type="copy" theme="filled" style={{color:COLORS.APPCOLOR}} onClick={()=>this.props.copyQuestion(this.props.index)}/>
            </Col>
            <Col span={1}>
              <Icon type="delete" theme="filled" style={{color:COLORS.APPCOLOR}} onClick={()=>this.props.deleteQuestion(this.props.index)}/>
            </Col>
          </Row>
      </Row>
    );
  }
}
const mapDispatchToProps = {
  updateQuestion,
  copyQuestion,
  deleteQuestion,
}
export default connect(null,mapDispatchToProps)(QuestionScale);
