import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Dropdown} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import SubGroupItem from './subGroupItem'
import {connect} from 'react-redux';
import {updateCurrentIndex,deleteSubGroup} from '../../actions/createAction';
import {Badge,FpBadgeMenu} from "../../fp-components/fp-minor"
import {COLORS} from  "../../constants"
class SubQuestionList extends Component {
  deleteSubGroup = () =>{
    let {index,questionIndex} = this.props;
    this.props.deleteSubGroup(questionIndex,index);
  }
  render() {
    console.log(this.props,"sub-group/subgrouplist.js")
    return (
      (this.props.list && this.props.list.length>0)?
      (
        <Row>
          <Row style={{marginBottom:'5px'}}>
            <Col span={3}>
              <Badge text={String.fromCharCode(parseInt(this.props.index)+65)} color={COLORS.SCALE_TYPE} style={{padding:'1px 7px'}}/>
            </Col>
            <Col offset={18} span={3} style={{textAlign:'right'}}>
              <Icon type="delete" theme="filled" style={{color:COLORS.APPCOLOR,textAlign:'right'}} onClick={()=>this.deleteSubGroup()} />
            </Col>
          </Row>
          <Row>
            {
              this.props.list.map((listItem,i) =>
                  <SubGroupItem listItem={listItem} index={i} parentIndex={this.props.index}  questionIndex={this.props.questionIndex} />
              )
            }
          </Row>
          <Row style={{marginBottom:'5px'}}>
            <Dropdown overlay={FpBadgeMenu(this.props.questionIndex,this.props.index)} >
              <Icon type="plus-circle" theme="filled" style={{color:COLORS.GREYCOLOR,fontSize:20}}/>
            </Dropdown>

          </Row>

        </Row>
      )
      :null
    );
  }
}
const mapDispatchToProps = {
  updateCurrentIndex,
  deleteSubGroup,
}
export default connect(null,mapDispatchToProps)(SubQuestionList);

