import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon} from 'antd'
import CopyDelete from '../images/copy.png'
import Delete from '../images/delete.png'

import SettingsText from './settings-components/text';
import SettingsNumber from './settings-components/number';
import SettingsScale from './settings-components/scale';
import SettingsLocation from './settings-components/location';
import SettingsDate from './settings-components/date';
import SettingsFile from './settings-components/file';
import SettingsDropDown from './settings-components/dropdown';
import SettingsEmail from './settings-components/email';
import SettingsMatrix from './settings-components/matrix';
import SettingsPrice from './settings-components/price';
import SettingsSign from './settings-components/sign';
import SettingsImage from './settings-components/image';
import SettingsScan from './settings-components/scan';
class QuestionType extends Component {
  render() {
    let currentComponent = undefined;
    let {currentQuestion,currentIndex} =this.props;
    if(currentQuestion)
    switch (currentQuestion.type) {
      case "text":
        currentComponent = <SettingsText listItem={currentQuestion} index={currentIndex}/>
        break
      case "number":
        currentComponent = <SettingsNumber listItem={currentQuestion} index={currentIndex}/>
        break
      case "scale":
        currentComponent = <SettingsScale listItem={currentQuestion} index={currentIndex}/>
        break
      case "location" :
        currentComponent = <SettingsLocation listItem={currentQuestion} index={currentIndex}/>
        break
      case "date" :
        currentComponent = <SettingsDate listItem={currentQuestion} index={currentIndex}/>
        break
      case "file" :
        currentComponent = <SettingsFile listItem={currentQuestion} index={currentIndex}/>
        break
      case "dropdown" :
        currentComponent = <SettingsDropDown listItem={currentQuestion} index={currentIndex}/>
        break
      case "email" :
        currentComponent = <SettingsEmail listItem={currentQuestion} index={currentIndex}/>
        break
      case "matrix" :
        currentComponent = <SettingsMatrix listItem={currentQuestion} index={currentIndex}/>
        break
      case "price" :
        currentComponent = <SettingsPrice listItem={currentQuestion} index={currentIndex}/>
        break
      case "sign" :
        currentComponent = <SettingsSign listItem={currentQuestion} index={currentIndex}/>
        break
      case "image" :
        currentComponent = <SettingsImage listItem={currentQuestion} index={currentIndex}/>
        break
      case "scan" :
        currentComponent = <SettingsScan listItem={currentQuestion} index={currentIndex}/>
        break

      default:
        currentComponent = <SettingsText listItem={currentQuestion} index={currentIndex}/>

    }
    if(currentQuestion===undefined)
    return(
      <div className="create-question-container padding-30">
          <div style={{textAlign:'center'}}>{"Create a question"}</div>
      </div>
    )
    else
    return (
      <div className="create-question-container padding-40">
          {currentComponent}
      </div>
    )

  }
}

export default QuestionType;
