import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Modal} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import EmailBadge from '../../images/email-badge.svg'
import {connect} from 'react-redux'
import {COLORS} from  "../../constants"
import {updateSubQuestion} from '../../actions/createAction';
import {Badge} from "../../fp-components/fp-minor"
import SettingsEmail from '../settings-components/email';
class QuestionEmail extends Component {
  updateQuestion = (event) => {
    let {index,parentIndex,questionIndex}=this.props;
    let questionObj = Object.assign({},this.props.listItem,{question:event.currentTarget.value});
  //  console.log(questionObj,"sub-components/text.js")
    this.props.updateSubQuestion(questionObj,questionIndex,parentIndex,index);
  }
  settingsToggle = () => {
    let {index,parentIndex,questionIndex}=this.props;
    let questionObj = Object.assign({},this.props.listItem,{settingsOpen:!this.props.listItem.settingsOpen});
  //  console.log(questionObj,"sub-components/text.js")
    this.props.updateSubQuestion(questionObj,questionIndex,parentIndex,index);
  }
  updateAnswer = (event) => {
    let {index,parentIndex,questionIndex}=this.props;
    //console.log(this.props.index);
    let questionObj = Object.assign({},this.props.listItem,{option:event.currentTarget.value});
   this.props.updateSubQuestion(questionObj,questionIndex,parentIndex,index);
  }
  render() {
    let {index,parentIndex,questionIndex,listItem}=this.props;
    return (
      <Row className="create-sub-question-item-container ">
        <Modal
          title="Settings"
          visible={this.props.listItem.settingsOpen}
          footer={null}
          onCancel={this.settingsToggle}
        >
          <SettingsEmail listItem={listItem} index={index} parentIndex={parentIndex} questionIndex={questionIndex} subGroup={1}  />
        </Modal>
          <Row>
          <Col span={4} className="badge-container">
              <Badge text={<span><img src={EmailBadge} style={{padding:'5px 7px 6px 1px'}} />{`${parseInt(this.props.index)+1}`}</span>} color={COLORS.EMAIL_TYPE} />
          </Col>
            <Col span={18}>
              <Input value={this.props.listItem.question} className="create-sub-question-input " onChange={(e)=>this.updateQuestion(e)}/>
            </Col>
            <Col span={1} style={{textAlign:'right'}}>

              <Icon type="setting" style={{color:COLORS.APPCOLOR}} onClick={()=>this.settingsToggle()}/>
            </Col>
            <Col span={1} style={{textAlign:'right'}}>
              <Icon type="delete" theme="filled" style={{color:COLORS.APPCOLOR}} onClick={()=>this.props.delete()}/>
            </Col>
          </Row>
          <Row className="second-row-container">
            <Col span={6}>
            </Col>
            <Col span={18}>
              {
                (this.props.listItem.settings.description)?
                <Input value={this.props.listItem.option} className="create-question-input-text-answer" onChange={(e)=>this.updateAnswer(e)}/>
                :
                null
              }
            </Col>
          </Row>

      </Row>
    );
  }
}
const mapDispatchToProps = {
  updateSubQuestion
}
export default connect(null,mapDispatchToProps)(QuestionEmail);
