import {
  UPDATE_QUESTIONS,
  UPDATE_QUESTION,
  COPY_QUESTION,
  DELETE_QUESTION,
  UPDATE_CURRENT_INDEX,
  DELETE_CURRENT_SUB_INDEX,
  DELETE_SUB_GROUP,
  UPDATE_SUB_INDEX
} from '../actions/types';

export const updateQuestions = (questions) => dispatch => {
  console.log("dispatch called")
    dispatch({
        type: UPDATE_QUESTIONS,
        questions,
    });
}
export const updateQuestion = (question,index) => dispatch => {
  console.log("UPDATE_QUESTION dispatch called");
  dispatch({
    type:UPDATE_QUESTION,
    question,
    index
  })
}
export const deleteQuestion = (index) => dispatch => {
  console.log("DELETE_QUESTION dispatch called",index);
  dispatch({
    type:DELETE_QUESTION,
    index
  })
}
export const copyQuestion = (index) => dispatch => {
  console.log("COPY_QUESTION dispatch called");
  dispatch({
    type:COPY_QUESTION,
    index
  })
}
export const updateCurrentIndex = (index) => dispatch => {
  console.log("UPDATE_CURRENT_INDEX dispatch called",index);
  dispatch({
    type:UPDATE_CURRENT_INDEX,
    index
  })
}
export const deleteSubGroup =  (questionIndex,index) => dispatch => {
  console.log("DELETE_CURRENT_SUB_INDEX dispatch called",questionIndex,index);
  dispatch({
    type:DELETE_SUB_GROUP,
    questionIndex,
    index
  })
}

export const deleteSubQuestion = (questionIndex,parentIndex,index) => dispatch => {
  console.log("DELETE_CURRENT_SUB_INDEX dispatch called",questionIndex,parentIndex,index);
  dispatch({
    type:DELETE_CURRENT_SUB_INDEX,
    questionIndex,
    parentIndex,
    index
  })
}
export const updateSubQuestion = (question,questionIndex,parentIndex,index) => dispatch => {
  console.log("UPDATE_SUB_INDEX dispatch called",questionIndex,parentIndex,index);
  dispatch({
    type:UPDATE_SUB_INDEX,
    question,
    questionIndex,
    parentIndex,
    index
  })
}
