import React from 'react';
import { Menu,Icon,Select } from 'antd';
import {COLORS} from "../constants";
import DropDownBadge from '../images/dropdown-badge.svg'
import EmailBadge from '../images/email-badge.svg'
import NumberBadge from '../images/number-badge.svg'
import ScaleBadge from '../images/scale-badge.svg'
import TextBadge from '../images/text-badge.svg'
import SignBadge from '../images/sign-badge.svg'
import {
  addQuestion,
  addSubQuestion,
  addSubGroupQuestion,
} from '../logic'
const Option = Select.Option;

export const Badge = (props) => {
  return (
    <div style={{display:'inline-block',background:props.color,padding:"1px 10px",borderRadius:15,color:'white',...props.style}}>
    {props.text}
    </div>
  )
}
const onClick= (key ,index,subGroupIndex) => {
  console.log(key,index)
  if(index!==undefined)
  {
    if(subGroupIndex!==undefined){
      addSubGroupQuestion(key ,index,subGroupIndex);
      return;
    }
    addSubQuestion(key,index);
    return;
  }
  addQuestion(key);
};

