import React, { Component } from 'react';
import {Layout,Dropdown} from 'antd'
import AddButton from '../images/add-button.svg'
import QuestionList from './questionlist'
import {FpBadgeMenu} from "../fp-components/fp-minor";
import {addQuestion} from '../logic'
class Question extends Component {
   onClick= ({ key }) => {
    console.log(key)
    addQuestion(key);
  };
  render() {
    let  {questions} = this.props;
    return (
      <div className="create-question-container padding-30">
          <div style={{maxHeight:'90%',overflowX:'scroll',padding:5}}>
            
          <QuestionList list={questions} />
          </div>
          <Dropdown overlay={FpBadgeMenu} >
          <img src={AddButton} alt="Add Button" className="add-question-button" />
          </Dropdown>
      </div>
    );
  }
}


export default Question;
