import React, { Component } from 'react'
import './create.css'
import {Layout,Row, Col} from 'antd'
import BackgroundImage from '../images/custom-background.png'
import Question from './question'
import QuestionType from './questiontype'
import Preview from './preview'
import {connect} from 'react-redux'
class Create extends Component {
  render() {
    let {questions,currentIndex} = this.props;
    console.log(currentIndex,"create/index.js")
    return (
      <Row style={{backgroundImage:`url(${BackgroundImage})`,width:'100%',height:'93%'}}>
        <Row className="height-full ">
          <Col span={9} xs={24} md={9} className="height-full padding-30">
              <Question questions={[...questions]}/>
          </Col>
          <Col span={8} xs={24} md={8} className="height-full padding-30">
              <QuestionType currentIndex={currentIndex} currentQuestion={questions[currentIndex]}/>
          </Col>
          <Col span={7} xs={24} md={7} className="height-full padding-30">
              <Preview currentIndex={currentIndex} currentQuestion={questions[currentIndex]}/>
          </Col>
        </Row>
      </Row>

    );
  }
}
const mapDispatchToProps = {

}
const mapStateToProps = (state) => ({
  questions: state.create.questions,
  currentIndex:state.create.currentIndex
})
export default connect(mapStateToProps,mapDispatchToProps)(Create);
