import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon} from 'antd'
import CopyDelete from '../images/copy.png'
import Delete from '../images/delete.png'
import {connect} from 'react-redux'
import {updateQuestion,copyQuestion,deleteQuestion} from '../actions/createAction';
import QuestionText from './question-components/text';
import QuestionNumber from './question-components/number';
import QuestionScale from './question-components/scale';
import QuestionLocation from './question-components/location';
import QuestionDate from './question-components/date';
import QuestionFile from './question-components/file';
import QuestionDropDown from './question-components/dropdown';
import QuestionEmail from './question-components/email';
import QuestionMatrix from './question-components/matrix';
import QuestionPrice from './question-components/price';
import QuestionSign from './question-components/sign';
import QuestionImage from './question-components/image';
import QuestionScan from './question-components/scan';
class QuestionItem extends Component {
  render() {
    let {listItem,index} =this.props;
    switch (listItem.type) {
      case "text":
        return <QuestionText listItem={listItem} index={index}/>
      case "number":
        return <QuestionNumber listItem={listItem} index={index}/>
      case "scale":
        return <QuestionScale listItem={listItem} index={index}/>
      case "location" :
        return <QuestionLocation listItem={listItem} index={index}/>
      case "date" :
        return <QuestionDate listItem={listItem} index={index}/>
      case "file" :
        return <QuestionFile listItem={listItem} index={index}/>
      case "dropdown" :
        return <QuestionDropDown listItem={listItem} index={index}/>
      case "email" :
        return <QuestionEmail listItem={listItem} index={index}/>
      case "matrix" :
        return <QuestionMatrix listItem={listItem} index={index}/>
      case "price" :
        return <QuestionPrice listItem={listItem} index={index}/>
      case "sign" :
        return <QuestionSign listItem={listItem} index={index}/>
      case "image" :
        return <QuestionImage listItem={listItem} index={index}/>
      case "scan" :
        return <QuestionScan listItem={listItem} index={index}/>

      default:
        return <QuestionText listItem={listItem} index={index}/>
    }
  }
}
const mapDispatchToProps = {
  updateQuestion,
  copyQuestion,
  deleteQuestion,
}
export default connect(null,mapDispatchToProps)(QuestionItem);
