import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Switch,Select,DatePicker} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'
import {COLORS} from  "../../constants"
import {FpSelectMenu,Badge} from "../../fp-components/fp-minor"
import {updateQuestion,copyQuestion,deleteQuestion} from '../../actions/createAction';
import {connect} from 'react-redux'
const Option = Select.Option;
class PreviewDate extends Component {
  updatePreview = (name,value) => {
    let {index,listItem}=this.props;
    let preview = {...listItem.preview};
    preview[name]=value;
    let questionObj = Object.assign({},this.props.listItem,{preview:preview});
    this.props.updateQuestion(questionObj,index);
  }

  render() {

    let {preview,settings} = this.props.listItem;
    let {listItem,index} = this.props;
    let backgroundColor = COLORS.APPCOLOR;
    return(
      <Row className="preview-body" style={{background:backgroundColor}}>
          <div style={{marginTop:'55%',fontSize:25,color:'white',marginLeft:20,marginBottom:10}}>
          {`${parseInt(index)+1}  ${listItem.question}`}
          </div>
          <div style={{padding:20}}>
              <DatePicker className="create-preview-input-date" style={{ width: '100%',backgroundColor:COLORS.APPCOLOR }} />
          </div>
      </Row>
    );
  }
}
const mapDispatchToProps = {
    updateQuestion,
    copyQuestion,
    deleteQuestion
}
export default connect(null,mapDispatchToProps)(PreviewDate);
