import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon} from 'antd'
import CopyDelete from '../images/copy.png'
import Delete from '../images/delete.png'
import QuestionItem from './questionItem'
import {connect} from 'react-redux';
import {updateCurrentIndex} from '../actions/createAction';
class QuestionList extends Component {
  getQuestion = (index) => {
    this.props.updateCurrentIndex(index);
  }
  render() {
    return (
      (this.props.list && this.props.list.length>0)?
      (
        this.props.list.map((listItem,i) => (
          <div onClick={()=> this.getQuestion(i)}>
            <Row className="create-question-item-container-box">
              <QuestionItem listItem={listItem} index={i}  />
            </Row>
          </div>
        ))
      ):null
    );
  }
}
const mapDispatchToProps = {
  updateCurrentIndex
}
export default connect(null,mapDispatchToProps)(QuestionList);
