import React, { Component } from 'react';
import {Layout,Row, Col,Input,Icon,Switch,Select} from 'antd'
import CopyDelete from '../../images/copy.png'
import Delete from '../../images/delete.png'

import {connect} from 'react-redux'
import {COLORS} from  "../../constants"
import {updateQuestion,updateSubQuestion} from '../../actions/createAction';
import {FpSelectMenu,Badge} from "../../fp-components/fp-minor"

const Option = Select.Option;
class QuestionLocation extends Component {
  updateSettings = (name,value) => {
    let {index,parentIndex,questionIndex,listItem}=this.props;

    let settings = {...listItem.settings};
    settings[name]=value;
    let questionObj = Object.assign({},this.props.listItem,{settings:settings});
    if(this.props.subGroup===1){
      this.props.updateSubQuestion(questionObj,questionIndex,parentIndex,index);
      return;
    }
    this.props.updateQuestion(questionObj,index);
  }
  render() {
    let {settings} = this.props.listItem
    return (
      <Row >
        {
            this.props.subGroup!==1 ? <h4>{'Settings'}</h4> : null
        }

          <Row>
            Question Type

            <FpSelectMenu value={this.props.listItem.type} />
          </Row>

          <Row className="margin-top-30">
              <Col span={12} style={{textAlign:'left'}}>
                  Required
              </Col>
              <Col span={12} style={{textAlign:'right'}}>
                  <Switch defaultChecked  checked={settings.required} onChange={(checked)=>this.updateSettings("required",checked)}  />
              </Col>
          </Row>
          <Row className="margin-top-30">
              <Col span={12} style={{textAlign:'left'}}>
                  Description
              </Col>
              <Col span={12} style={{textAlign:'right'}}>
                  <Switch defaultChecked  checked={settings.description} onChange={(checked)=>this.updateSettings("description",checked)}  />
              </Col>
          </Row>

          <Row className="margin-top-30">
              <Col span={12} style={{textAlign:'left'}}>
                  Image/Video
              </Col>
              <Col span={12} style={{textAlign:'right'}}>
                  <Badge text={"Add"} color={COLORS.APPCOLOR} />
              </Col>
          </Row>
          {
            this.props.subGroup!==1 &&
            <Row className="margin-top-30">
                <Col span={12} style={{textAlign:'left'}}>
                    Subgroup
                </Col>
                <Col span={12} style={{textAlign:'right'}}>
                    <Switch defaultChecked  checked={settings.subGroup} onChange={(checked)=>this.updateSettings("subGroup",checked)}  />
                </Col>
            </Row>
          }
          <Row className="margin-top-30">
              <Col span={12} style={{textAlign:'left'}}>
                  Geolock
              </Col>
              <Col span={12} style={{textAlign:'right'}}>
                  <Switch defaultChecked  checked={settings.geolock} onChange={(checked)=>this.updateSettings("geolock",checked)}  />
              </Col>
          </Row>
          {
            settings.geolock &&
            <Row>
            <Row className="margin-top-30">
                <Col span={24} style={{textAlign:'left'}}>
                    Question from External sources
                </Col>
            </Row>
            <Row className="margin-top-15">
              <Col span={6}>
                <Select
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="excel"
                  value={settings.externalSourcesC1}
                   onChange={(value)=>this.updateSettings("externalSourcesC1",value)}
                >
                  <Option value="excel">excel</Option>

                </Select>
              </Col>
              <Col span={16} offset={2}>
                <Select
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="column"
                  value={settings.externalSourcesC2} onChange={(value)=>this.updateSettings("externalSourcesC2",value)}
                >
                  <Option value="column">excel</Option>

                </Select>
              </Col>
            </Row>
            <Row className="margin-top-30">
                <Col span={12} style={{textAlign:'left'}}>
                    Latitude
                </Col>
                <Col span={12} style={{textAlign:'right'}}>

                </Col>
            </Row>
            <Row className="margin-top-15">
              <Col span={6}>
                <Select
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="excel"
                  value={settings.latitudeC1} onChange={(value)=>this.updateSettings("latitudeC1",value)}
                >
                  <Option value="excel">excel</Option>

                </Select>
              </Col>
              <Col span={16} offset={2}>
                <Select
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="column"
                  value={settings.latitudeC2} onChange={(value)=>this.updateSettings("latitudeC2",value)}
                >
                  <Option value="column">excel</Option>

                </Select>
              </Col>
            </Row>
            <Row className="margin-top-30">
                <Col span={12} style={{textAlign:'left'}}>
                    Longitude
                </Col>
                <Col span={12} style={{textAlign:'right'}}>

                </Col>
            </Row>
            <Row className="margin-top-15">
              <Col span={6}>
                <Select
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="excel"
                  value={settings.longitudeC1} onChange={(value)=>this.updateSettings("longitudeC1",value)}
                >
                  <Option value="excel">excel</Option>

                </Select>
              </Col>
              <Col span={16} offset={2}>
                <Select
                  showSearch
                  style={{ width: "100%" }}
                  placeholder="column"
                  value={settings.longitudeC2} onChange={(value)=>this.updateSettings("longitudeC2",value)}
                >
                  <Option value="column">excel</Option>

                </Select>
              </Col>
            </Row>
            </Row>
          }


      </Row>
    );
  }
}
const mapDispatchToProps = {
  updateQuestion,
  updateSubQuestion
}
export default connect(null,mapDispatchToProps)(QuestionLocation);
